"use client"

import { Fragment, useMemo } from 'react'
import { Card } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Filter, Download, ChevronLeft, ChevronRight, ScanEye } from 'lucide-react'
import type { EquipmentRecord } from '../data/mock-equipment'

interface EquipmentTableProps {
  equipment: EquipmentRecord[]
  filteredTotal: number
  total: number
  page: number
  totalPages: number
  onPageChange: (page: number) => void
}

const statusVariantMap: Record<string, Parameters<typeof Badge>[0]['variant']> = {
  Operativo: 'success',
  'Correctivo activo': 'error',
  'Fuera de servicio': 'error',
  'En calibración': 'info',
  'Preventivo pendiente': 'warning'
}

const formatter = new Intl.DateTimeFormat('es-CO', {
  day: '2-digit',
  month: 'short',
  year: 'numeric'
})

const formatDate = (value: string) => {
  if (!value) return 'No programado'
  const date = new Date(value)
  return Number.isNaN(date.getTime()) ? value : formatter.format(date)
}

export function EquipmentTable({ equipment, filteredTotal, total, page, totalPages, onPageChange }: EquipmentTableProps) {
  const showEmptyState = equipment.length === 0
  const footerLabel = useMemo(() => {
    if (filteredTotal === total) return `${total} equipos registrados`
    return `${filteredTotal} de ${total} equipos muestran estos criterios`
  }, [filteredTotal, total])

  const handlePrev = () => onPageChange(Math.max(1, page - 1))
  const handleNext = () => onPageChange(Math.min(totalPages, page + 1))

  return (
    <Card
      title="Inventario maestro"
      description="Controla estado operativo, responsables y ventanas de mantenimiento por equipo."
      className="bg-white shadow-xl"
      actions={
        <div className="flex items-center gap-2">
          <Button variant="ghost" size="sm" className="gap-2 text-slate-600">
            <Filter className="h-4 w-4" />
            Guardar vista
          </Button>
          <Button variant="secondary" size="sm" className="gap-2 border border-slate-200 text-slate-900">
            <Download className="h-4 w-4" />
            Exportar CSV
          </Button>
        </div>
      }
    >
      <div className="flex flex-col gap-4">
        <div className="flex flex-wrap items-center justify-between gap-3 text-sm text-slate-500">
          <span className="inline-flex items-center gap-2 rounded-2xl bg-slate-50 px-3 py-1 font-medium text-slate-600">
            <ScanEye className="h-4 w-4 text-slate-400" />
            {footerLabel}
          </span>
          <div className="inline-flex items-center gap-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={handlePrev}
              disabled={page === 1}
              className="text-slate-500"
            >
              <ChevronLeft className="h-4 w-4" />
              Anterior
            </Button>
            <span className="text-xs font-semibold text-slate-500">
              Página {page} / {totalPages}
            </span>
            <Button
              variant="ghost"
              size="sm"
              onClick={handleNext}
              disabled={page === totalPages}
              className="text-slate-500"
            >
              Siguiente
              <ChevronRight className="h-4 w-4" />
            </Button>
          </div>
        </div>

        {showEmptyState ? (
          <div className="flex h-48 flex-col items-center justify-center gap-2 rounded-2xl border border-dashed border-slate-200 bg-slate-50 text-center text-slate-500">
            <p className="text-base font-semibold">No hay equipos con estos filtros</p>
            <p className="text-sm">Ajusta la búsqueda o reinicia los filtros para ver el inventario completo.</p>
          </div>
        ) : (
          <div className="overflow-hidden rounded-2xl border border-slate-200">
            <table className="min-w-full divide-y divide-slate-200 text-sm">
              <thead className="bg-slate-50 text-xs font-semibold uppercase tracking-wide text-slate-500">
                <tr>
                  <th className="px-4 py-3 text-left">Equipo</th>
                  <th className="px-4 py-3 text-left">Ubicación / Proceso</th>
                  <th className="px-4 py-3 text-left">Estado</th>
                  <th className="px-4 py-3 text-left">Mantenimiento</th>
                  <th className="px-4 py-3 text-left">Responsable</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-slate-100 bg-white">
                {equipment.map((item) => (
                  <tr key={item.id} className="hover:bg-slate-50">
                    <td className="px-4 py-4">
                      <p className="text-sm font-semibold text-slate-900">{item.name}</p>
                      <p className="text-xs text-slate-500">Código interno {item.code}</p>
                    </td>
                    <td className="px-4 py-4 text-sm text-slate-600">
                      <p className="font-medium text-slate-900">{item.location}</p>
                      <p className="text-xs text-slate-500">{item.process} · {item.area}</p>
                    </td>
                    <td className="px-4 py-4">
                      <div className="flex flex-col gap-1">
                        <Badge variant={statusVariantMap[item.status] ?? 'info'} size="sm" className="w-fit">
                          {item.status}
                        </Badge>
                        <span className="text-xs text-slate-500">{item.maintenanceType} · Criticidad {item.criticality}</span>
                      </div>
                    </td>
                    <td className="px-4 py-4 text-sm text-slate-600">
                      <p className="font-medium text-slate-900">Próximo: {formatDate(item.nextMaintenance)}</p>
                      <p className="text-xs text-slate-500">Último: {formatDate(item.lastMaintenance)}</p>
                    </td>
                    <td className="px-4 py-4 text-sm text-slate-600">
                      <p className="font-medium text-slate-900">{item.assignedTo}</p>
                      <p className="text-xs text-slate-500">Proveedor {item.provider}</p>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </Card>
  )
}

// Export temporal para mantener compatibilidad con la importación anterior
export { EquipmentTable as EquipmentTablePreview }

