import { Card } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

interface StatusSummaryItem {
  label: string
  value: number
  description: string
}

interface InventorySummaryBoardProps {
  summary: StatusSummaryItem[]
  total: number
  filteredTotal: number
}

const badgeVariants = ['success', 'warning', 'info', 'error'] as const
const percentageFormat = new Intl.NumberFormat('es-CO', {
  style: 'percent',
  maximumFractionDigits: 0
})

export function InventorySummaryBoard({ summary, total, filteredTotal }: InventorySummaryBoardProps) {
  return (
    <section className="grid gap-4 md:grid-cols-2 xl:grid-cols-5">
      {summary.map((item, index) => (
        <Card key={item.label} className="bg-white px-6 py-5 shadow-lg">
          <div className="flex flex-col gap-3">
            <div className="flex items-center justify-between">
              <p className="text-sm font-medium text-slate-500">{item.label}</p>
              <Badge variant={badgeVariants[index] ?? 'secondary'} size="sm">
                {percentageFormat.format(item.value / Math.max(1, filteredTotal))}
              </Badge>
            </div>
            <p className="text-3xl font-semibold text-slate-900">{item.value}</p>
            <p className="text-sm text-slate-500">{item.description}</p>
          </div>
        </Card>
      ))}
      <Card className="bg-emerald-600 px-6 py-5 text-white md:col-span-2 xl:col-span-1">
        <p className="text-sm uppercase tracking-wide text-white/80">Inventario trazado</p>
        <p className="mt-2 text-3xl font-semibold">{filteredTotal}</p>
        <p className="text-sm text-white/80">de {total} equipos coinciden con los filtros seleccionados.</p>
      </Card>
    </section>
  )
}
