"use client"

import { Card } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Select } from '@/components/ui/select'
import { Button } from '@/components/ui/button'
import { Filter, RotateCcw, Search, X } from 'lucide-react'
import type {
  EquipmentFilterOptions,
  EquipmentFiltersState
} from '@/features/inventory/hooks/use-equipment-inventory'

type FilterKey = keyof EquipmentFiltersState

interface InventoryFiltersProps {
  search?: string
  onSearchChange?: (value: string) => void
  filters?: EquipmentFiltersState
  onFilterChange?: <K extends FilterKey>(key: K, value: EquipmentFiltersState[K]) => void
  options?: EquipmentFilterOptions
  activeFiltersCount?: number
  onReset?: () => void
}

const FILTER_LABELS: Record<FilterKey, string> = {
  location: 'Sede',
  process: 'Proceso',
  status: 'Estado',
  criticality: 'Criticidad',
  calibrationStatus: 'Calibración'
}

const FILTER_PLACEHOLDERS: Record<FilterKey, string> = {
  location: 'Todas las sedes',
  process: 'Todos los procesos',
  status: 'Todos los estados',
  criticality: 'Todas las criticidades',
  calibrationStatus: 'Todos los estados'
}

const FALLBACK_FILTERS: EquipmentFiltersState = {
  location: 'all',
  process: 'all',
  status: 'all',
  criticality: 'all',
  calibrationStatus: 'all'
}

export function InventoryFilters({
  search = '',
  onSearchChange = () => {},
  filters = FALLBACK_FILTERS,
  onFilterChange = () => {},
  options,
  activeFiltersCount = 0,
  onReset = () => {}
}: InventoryFiltersProps) {
  const resolvedOptions: EquipmentFilterOptions = options ?? {
    locations: [],
    processes: [],
    statuses: [],
    criticalities: [],
    calibrations: []
  }

  const selectConfig: Array<{ key: FilterKey; data: string[] }> = [
    { key: 'location', data: resolvedOptions.locations },
    { key: 'process', data: resolvedOptions.processes },
    { key: 'status', data: resolvedOptions.statuses },
    { key: 'criticality', data: resolvedOptions.criticalities },
    { key: 'calibrationStatus', data: resolvedOptions.calibrations }
  ]

  const activeFilters = (Object.entries(filters) as Array<[FilterKey, string]>).filter(([, value]) => value !== 'all')

  return (
    <Card
      title="Filtros inteligentes"
      description="Segmenta el inventario por sede, proceso, estado operativo y ventanas de calibración."
      className="bg-white"
      actions={
        <Button
          variant="ghost"
          size="sm"
          className="text-slate-600"
          onClick={onReset}
          disabled={activeFiltersCount === 0 && !search}
        >
          <RotateCcw className="h-4 w-4" />
          Reiniciar
        </Button>
      }
    >
      <div className="flex flex-col gap-6">
        <div className="flex flex-col gap-4 lg:flex-row lg:items-center">
          <div className="relative flex-1">
            <Search className="pointer-events-none absolute left-4 top-1/2 h-4 w-4 -translate-y-1/2 text-slate-400" />
            <Input
              value={search}
              onChange={(event) => onSearchChange(event.target.value)}
              placeholder="Busca por código, equipo o responsable"
              className="pl-11"
              aria-label="Búsqueda en inventario"
            />
          </div>
          <div className="flex items-center gap-3">
            <div className="flex items-center gap-2 rounded-2xl border border-slate-200 bg-slate-50 px-3 py-2 text-sm text-slate-600">
              <Filter className="h-4 w-4 text-slate-500" />
              {activeFiltersCount > 0 ? (
                <span>{activeFiltersCount} filtros activos</span>
              ) : (
                <span>Sin filtros aplicados</span>
              )}
            </div>
          </div>
        </div>

        <div className="grid gap-4 md:grid-cols-2 xl:grid-cols-5">
          {selectConfig.map(({ key, data }) => (
            <label key={key} className="flex flex-col gap-2 text-sm font-medium text-slate-600">
              {FILTER_LABELS[key]}
              <Select
                value={filters[key]}
                onChange={(event) => onFilterChange(key, event.target.value as EquipmentFiltersState[typeof key])}
                className="h-11 rounded-2xl border-slate-200 bg-white text-slate-900"
              >
                <option value="all">{FILTER_PLACEHOLDERS[key]}</option>
                {data.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </Select>
            </label>
          ))}
        </div>

        <div className="rounded-2xl border border-dashed border-slate-200 bg-slate-50/70 px-4 py-3 text-sm text-slate-600">
          <p className="mb-3 font-medium text-slate-700">Filtros aplicados</p>
          {activeFilters.length > 0 ? (
            <div className="flex flex-wrap gap-2">
              {activeFilters.map(([key, value]) => (
                <span
                  key={`${key}-${value}`}
                  className="inline-flex items-center gap-2 rounded-full bg-white px-3 py-1 text-xs font-medium text-slate-700 shadow-sm ring-1 ring-slate-200"
                >
                  {FILTER_LABELS[key]}: {value}
                  <button
                    type="button"
                    className="rounded-full p-0.5 text-slate-400 transition hover:bg-slate-100 hover:text-slate-600"
                    onClick={() => onFilterChange(key, 'all')}
                    aria-label={`Eliminar filtro de ${FILTER_LABELS[key]}`}
                  >
                    <X className="h-3.5 w-3.5" />
                  </button>
                </span>
              ))}
            </div>
          ) : (
            <p className="text-xs text-slate-500">No has aplicado filtros adicionales. Usa los selectores superiores para segmentar el inventario.</p>
          )}
        </div>
      </div>
    </Card>
  )
}

// Export temporal para mantener compatibilidad con la página mientras se actualiza la importación
export { InventoryFilters as FiltersPreview }

