"use client"

import { EquipmentTable } from '@/features/inventory/components/equipment-table'
import { InventoryFilters } from '@/features/inventory/components/filters-preview'
import { InventorySummaryBoard } from '@/features/inventory/components/summary-board'
import { useEquipmentInventory } from '@/features/inventory/hooks/use-equipment-inventory'

export default function InventoryPage() {
  const {
    search,
    setSearch,
    filters,
    setFilter,
    resetFilters,
    equipment,
    filteredTotal,
    total,
    page,
    totalPages,
    goToPage,
    availableFilters,
    statusSummary,
    activeFiltersCount
  } = useEquipmentInventory()

  return (
    <div className="space-y-8">
      <header className="flex flex-wrap items-center justify-between gap-4 rounded-3xl border border-white/10 bg-white/5 p-6">
        <div>
          <h1 className="text-3xl font-semibold text-white">Inventario</h1>
          <p className="text-sm text-white/60">
            Control integral del parque biomédico, su estado operativo y la vigencia de mantenimiento. Toda acción aquí
            se sincronizará con FastAPI + PostgreSQL.
          </p>
        </div>
        <div className="rounded-2xl border border-white/10 bg-white/10 px-4 py-2 text-xs text-white/80">
          {total} equipos registrados · {statusSummary[1]?.value ?? 0} programados · {statusSummary[2]?.value ?? 0} correctivos activos
        </div>
      </header>

      <InventorySummaryBoard summary={statusSummary} total={total} filteredTotal={filteredTotal} />

      <InventoryFilters
        search={search}
        onSearchChange={setSearch}
        filters={filters}
        onFilterChange={setFilter}
        options={availableFilters}
        activeFiltersCount={activeFiltersCount}
        onReset={resetFilters}
      />

      <EquipmentTable
        equipment={equipment}
        filteredTotal={filteredTotal}
        total={total}
        page={page}
        totalPages={totalPages}
        onPageChange={goToPage}
      />
    </div>
  )
}

